@import "./../variables";
.sort {
	position: relative;
	&__label {
		display: flex;
		align-items: center;

		svg {
			margin-right: 8px;
			transition: 0.3s;
			transform: rotate(180deg);

			&.rotated {
				transform: rotate(0deg);
			}
		}

		b {
			margin-right: 8px;
		}

		span {
			color: $orange;
			border-bottom: 1px dashed $orange;
			cursor: pointer;
		}
	}

	&__popup {
		position: absolute;
		right: 0;
		margin-top: 15px;
		background: #ffffff;
		box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
		border-radius: 10px;
		overflow: hidden;
		padding: 10px 0;
		width: 160px;

		ul {
			overflow: hidden;
			li {
				padding: 12px 20px;
				cursor: pointer;

				&.active,
				&:hover {
					background: rgba(254, 95, 30, 0.05);
				}

				&.active {
					font-weight: bold;
					color: $orange;
				}
			}
		}
	}
}

.order-success {
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	gap: 15px;
	font-size: 22px;
	font-weight: 600;
}
